.l-header {
  z-index: 100;
  padding-inline: 40px;
  padding-top: 40px;
  position: fixed;
  width: 100%;
  /* スクリーンリーダー */
}
.l-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.l-header__nav {
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  .l-header__nav {
    display: none;
  }
}
.l-header__menu {
  display: flex;
  align-items: center;
  flex-direction: row;
  font-family: Arial, sans-serif;
  text-transform: uppercase;
}
.l-header__menu-item {
  margin-right: 40px;
}
.l-header__menu-link {
  position: relative;
}
.l-header__menu-link:hover::before {
  transform: scaleX(1);
  transform-origin: left center;
}
.l-header__menu-link::before {
  position: absolute;
  bottom: -1.2rem;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #111111;
  content: "";
  transition: transform 0.5s cubic-bezier(0.33, 1, 0.68, 1);
  transform: scaleX(0);
  transform-origin: right center;
}
.l-header__submenu {
  padding-top: 100px;
  width: 639px;
}
.l-header__submenu-inner {
  display: flex;
  flex-direction: row;
}
.l-header__submenu-image {
  width: 289px;
  height: auto;
}
.l-header__submenu-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.l-header__submenu-list {
  width: 350px;
}
.l-header__submenu-item {
  height: 33.3%;
  padding: 2em 2em 1em 2em;
}
.l-header__submenu-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.l-header__submenu-link:hover {
  color: #88b999;
}
.l-header__submenu-title {
  display: inline-block;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.2rem;
}
.l-header__submenu-title .eng {
  font-family: Arial, sans-serif;
  font-size: 2.2rem;
  font-weight: bold;
  text-transform: none;
}
.l-header__hamburger-btn {
  all: unset;
  outline: revert;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  width: 46px;
  aspect-ratio: 1/1;
  display: grid;
  place-items: center;
  color: black;
  justify-items: start;
}
@media (min-width: 1024px) {
  .l-header__hamburger-btn {
    display: none;
  }
}
.l-header__hamburger-btn-bar, .l-header__hamburger-btn::before, .l-header__hamburger-btn::after {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background-color: currentColor;
  grid-area: 1/1;
  transition: transform 0.5s;
  text-align: left;
}
.l-header__hamburger-btn::before {
  transform: translateY(-12px);
  width: 100%;
}
.l-header__hamburger-btn::after {
  transform: translateY(12px);
  width: 25%;
}
.l-header__sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.is-none {
  display: none;
}

.js-submenu-trigger {
  position: relative;
}

.js-submenu {
  position: absolute;
  top: 0px;
  left: -200px;
}

.open {
  position: fixed;
  overflow: hidden;
  width: 100%;
}
.open .l-header__logo {
  z-index: 200;
}
.open .l-header__hamburger-btn-bar {
  transform: scale(0);
}
.open .l-header__hamburger-btn::before {
  transform: translateY(0) rotate(45deg);
}
.open .l-header__hamburger-btn::after {
  transform: translateY(0) rotate(-45deg);
  width: 100%;
}
.open .l-header__hamburger-btn {
  z-index: 200;
}
.open .l-mobile-nav {
  pointer-events: auto;
  opacity: 1;
}

.l-mobile-nav {
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s linear;
}

.l-header.js-view .l-header__menu {
  opacity: 1;
}
.l-header.js-hide .l-header__menu {
  opacity: 0;
  transition: opacity 0.3s linear;
}

.l-footer {
  background-color: #182f20;
  color: #ffffff;
}
.l-footer__inner {
  padding: 13.5%;
}
.l-footer__main {
  margin-top: 5rem;
  display: flex;
  justify-content: space-between;
}
.l-footer__policy {
  margin-top: 5rem;
  text-align: right;
}
.l-footer__policy small {
  margin-left: 5rem;
}
.l-footer__address {
  line-height: 1.8em;
}
.l-footer__list {
  display: flex;
  flex-flow: row wrap;
  width: 300px;
  margin-left: 1rem;
}
.l-footer__item {
  padding: 2rem;
  width: 150px;
  position: relative;
  padding-left: 1rem;
  text-transform: uppercase;
}
.l-footer__item:before {
  content: "";
  width: 0.5rem;
  height: 0.5rem;
  background: #88b999;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 2.7rem;
}
.l-footer__arrow {
  margin-top: 5rem;
  text-align: right;
  padding-right: 5rem;
}
.l-mobile-nav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  display: flex;
  flex-direction: column;
  gap: 22px;
  color: #111111;
  background-color: #d9e3dd;
  padding: 20% 10% 10% 10%;
}
.l-mobile-nav__menu {
  font-family: Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
}
.l-mobile-nav__menu-item {
  margin-top: 1rem;
  border-bottom: 1px solid #cbcbcb;
  padding-bottom: 1rem;
}
.l-mobile-nav__submenu {
  margin-top: 1rem;
  font-size: 1.4rem;
}
.l-mobile-nav__submenu-item {
  display: flex;
  align-items: center;
  margin-top: 2rem;
  font-weight: normal;
}
.l-mobile-nav__submenu-item::before {
  border-top: 1px solid;
  content: "";
  width: 1rem; /* 線の長さ */
  margin-right: 1rem;
}
.l-mobile-nav__subtitle {
  font-family: Arial, sans-serif;
  font-size: 1.2rem;
  font-weight: normal;
  margin-bottom: 1rem;
}
.l-mobile-nav__address {
  font-size: 1.2rem;
  font-weight: normal;
  line-height: 2rem;
}
.l-mobile-nav__policy {
  font-size: 1.2rem;
  font-weight: normal;
  color: #b4b4b4;
}
.l-mobile-nav__btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.l-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 7;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0;
  pointer-events: none;
}

.l-hero__main-wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.l-hero__heading-wrapper {
  margin-right: -10%;
  z-index: 1;
}
.l-hero__image {
  width: 73%;
  height: 604px;
}
.l-hero__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.l-hero__breadcrumbs {
  text-align: right;
}
.l-hero__breadcrumbs a {
  margin-right: 1rem;
  text-transform: uppercase;
  text-decoration: underline;
}
.l-hero__breadcrumbs a::after {
  content: "";
  display: inline-block;
  width: 1.5rem;
  border-bottom: 1px solid;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
}

.l-sub-footer {
  display: flex;
}
.l-sub-footer__container {
  width: 50%;
}
.l-sub-footer__container a {
  position: relative;
}
.l-sub-footer__container a:hover figure {
  overflow: hidden;
}
.l-sub-footer__container a:hover img {
  transform: scale(1.1);
}
.l-sub-footer__text-wrapper {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  font-weight: bold;
  color: #ffffff;
}
.l-sub-footer__text--en {
  font-size: 6.4rem;
  font-family: Arial, sans-serif;
}
.l-sub-footer__text {
  font-size: 1.5rem;
  text-align: center;
}

.c-arrow {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  text-decoration: none;
}
.c-arrow::before, .c-arrow::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  content: "";
  vertical-align: middle;
}
.c-arrow--round::before {
  box-sizing: border-box;
  width: 3rem;
  height: 3rem;
  border: 1px solid;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
.c-arrow--round::after {
  left: 0.8rem;
  width: 1rem;
  height: 1rem;
  border-top: 1px solid;
  border-right: 1px solid;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.c-arrow--round-top::before {
  box-sizing: border-box;
  width: 3rem;
  height: 3rem;
  border: 1px solid;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
.c-arrow--round-top::after {
  left: 0.9rem;
  top: 0.2rem;
  width: 1rem;
  height: 1rem;
  border-top: 1px solid;
  border-right: 1px solid;
  -webkit-transform: rotate(315deg);
  transform: rotate(315deg);
}

.c-arrow-bottom {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  margin: 0 1rem 0.2rem 1rem;
  border-bottom: 1px solid;
  border-right: 1px solid;
  transform: rotate(45deg);
}

.c-heading__title {
  font-family: Arial, sans-serif;
  font-size: 6.4rem;
  font-weight: bold;
  margin-top: 5rem;
  margin-left: 2rem;
  position: relative;
}
.c-heading__title::before {
  position: absolute;
  top: 1rem;
  left: -2rem;
  display: block;
  width: 1rem;
  border-radius: 50%;
  content: "";
  color: black;
  height: 1rem;
  background-color: #88b999;
}
.c-heading__subtitle {
  font-weight: 500;
  color: #88b999;
  margin-left: 2rem;
  margin-top: 0.5rem;
}

.c-sub-heading {
  min-width: 355px;
}
.c-sub-heading__title {
  font-family: Arial, sans-serif;
  font-size: 3.6rem;
  font-weight: bold;
}
.c-sub-heading__subtitle {
  font-size: 1.4rem;
  margin-top: 0.5rem;
  color: #88b999;
}

.c-desc {
  width: 355px;
}
.c-desc__text {
  margin-top: 3rem;
  font-size: 1.5rem;
  letter-spacing: 0.5rem;
  line-height: 1.5em;
}
.c-desc__link {
  margin-top: 3rem;
  text-align: right;
  font-family: Arial, sans-serif;
  color: #88b999;
}
.c-desc__link a {
  display: flex;
  justify-content: flex-end;
  margin-right: 4rem;
  gap: 1rem;
}
.c-text-loop {
  position: relative;
  height: 10rem;
  display: block;
}
.c-text-loop__textWrap {
  position: absolute;
  bottom: 0;
  display: block;
  overflow: hidden;
  width: 100%;
}
.c-text-loop__text {
  display: inline-block;
  margin: 0;
  padding-left: 100%;
  color: rgba(0, 0, 0, 0);
  white-space: nowrap;
  letter-spacing: 0em;
  font-weight: 600;
  font-size: 10rem;
  line-height: 1.12;
  animation: loopText 16s linear infinite;
  -webkit-text-stroke: 2px #fff;
}
.c-text-loop__text span {
  font-family: Arial, sans-serif;
  text-transform: uppercase;
}
.c-text-loop__link {
  position: absolute;
  right: 20%;
  margin-top: 3rem;
  text-align: right;
  font-family: Arial, sans-serif;
  color: #88b999;
}
.c-text-loop__link a {
  display: flex;
  justify-content: flex-end;
  margin-right: 4rem;
  gap: 1rem;
}
@keyframes loopText {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
.c-definition__list-item {
  display: flex;
  padding-top: 4rem;
  padding-bottom: 4rem;
  border-top: 1px solid #cbcbcb;
}
.c-definition__list-item:last-child {
  border-bottom: 1px solid #cbcbcb;
}
.c-definition__term {
  width: 160px;
  font-size: 1.5rem;
  letter-spacing: 0.3rem;
}
.c-definition__desc {
  letter-spacing: 0.3rem;
}

.c-inner {
  max-width: 1220px;
  margin: 0 auto;
  padding: 10px 30px;
}

.c-btn {
  width: 140px;
  display: block;
  padding: 1em;
  font-family: Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  text-align: center;
  background-color: #111111;
  color: #ffffff;
  border: solid 2px #111111;
  transition: background-color 0.5s;
}
.c-btn:hover {
  background-color: #ffffff;
  color: #111111;
}

.c-btn--accent {
  width: 140px;
  display: block;
  padding: 1em;
  font-family: Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.3rem;
  background-color: #ebc110;
  color: #ffffff;
  border: solid 2px #ebc110;
  transition: background-color 0.5s;
}
.c-btn--accent:hover {
  background-color: #ffffff;
  color: #ebc110;
}

.p-home-kv {
  background-color: #ffffff;
}
.p-home-kv__grid-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1080px;
  height: 100vh;
}
.p-home-kv__swiper-container {
  width: 100%;
  height: 900px;
  height: 100vh;
  grid-column: 3/3;
  grid-row: 1;
}
