/* スクロールアニメ */
.p-scroll {
  display: inline-block;
  position: absolute;
  right: 3rem;
  bottom: 0;
  z-index: 2;
  padding: 10px 10px 110px;
  height: 11rem;
  overflow: hidden;
  color: #fff;
}

.p-scroll::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 1px;
  height: 100px;
  background-color: #fff;
  animation: arrow 3s ease 0s infinite normal;
}

.p-scroll::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 1px;
  height: 100px;
  background-color: #111;
}

@keyframes arrow {
  0% {
    transform: translate3d(-50%, 0, 0);
  }
  60% {
    transform: translate3d(-50%, 11rem, 0);
  }
  100% {
    transform: translate3d(-50%, 11rem, 0);
  }
}
